/* Global Variables */
/* ==========================================================================
   Custom Post Types styles
   ========================================================================== */
/* ==========================================================================
# Restaurant Menu List style - begin
========================================================================== */
.mkdf-restaurant-menu-list {
  text-align: left;
}

.mkdf-restaurant-menu-list .mkdf-restaurant-menu-list-category {
  position: relative;
  padding-bottom: 37px;
  margin: 0 0 13px;
}

.mkdf-restaurant-menu-list .mkdf-restaurant-menu-list-category:after {
  content: "";
  height: 20px;
  position: absolute;
  background: #a1a1a1;
  left: 0;
  top: 41px;
  width: 1px;
}

.mkdf-restaurant-menu-list.mkdf-rml-center-left .mkdf-restaurant-menu-list-category span {
  display: inline-block;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  transform: translateX(-50%);
}

.mkdf-restaurant-menu-list ul {
  list-style-type: none;
}

.mkdf-restaurant-menu-list ul li {
  margin-bottom: 40px;
}

.mkdf-restaurant-menu-list ul li:last-child {
  margin-bottom: 0;
}

.mkdf-restaurant-menu-list .mkdf-rml-title-holder {
  display: inline-block;
  white-space: nowrap;
  padding-right: 1px;
}

.mkdf-restaurant-menu-list .mkdf-rml-price-holder {
  display: inline-block;
  padding-left: 7px;
  white-space: nowrap;
}

.mkdf-restaurant-menu-list .mkdf-rml-price-holder .mkdf-rml-price {
  display: inline-block;
}

.mkdf-restaurant-menu-list .mkdf-rml-price-holder .mkdf-rml-price:before {
  content: '...';
}

.mkdf-restaurant-menu-list .mkdf-rml-bottom-holder {
  position: relative;
  top: 7px;
}

.mkdf-restaurant-menu-list .mkdf-rml-description-holder {
  float: left;
  font-size: 13px;
  line-height: 20px;
}

.mkdf-restaurant-menu-list .mkdf-rml-description-holder:before {
  content: '(';
  margin-right: -3px;
}

.mkdf-restaurant-menu-list .mkdf-rml-description-holder:after {
  content: ')';
  margin-left: -3px;
}

.mkdf-restaurant-menu-list.mkdf-rml-with-featured-image .mkdf-rml-item-content {
  padding-left: 110px;
}

.mkdf-restaurant-menu-list.mkdf-rml-with-featured-image .mkdf-rml-item-image {
  float: left;
  width: 83px;
  position: relative;
}

.mkdf-restaurant-menu-list.mkdf-rml-with-featured-image .mkdf-rml-item-image a {
  display: block;
}

.mkdf-restaurant-menu-list.mkdf-rml-with-featured-image .mkdf-rml-item-image img {
  vertical-align: middle;
}

.mkdf-restaurant-menu-list.mkdf-rml-light .mkdf-restaurant-menu-list-category,
.mkdf-restaurant-menu-list.mkdf-rml-light .mkdf-rml-description-holder,
.mkdf-restaurant-menu-list.mkdf-rml-light .mkdf-rml-title,
.mkdf-restaurant-menu-list.mkdf-rml-light .mkdf-rml-price,
.mkdf-restaurant-menu-list.mkdf-rml-light .mkdf-rml-label {
  color: #fff;
}

.mkdf-restaurant-menu-list.mkdf-rml-light .mkdf-rml-line {
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
}

/* ==========================================================================
# Restaurant Menu List style - end
========================================================================== */
/* ==========================================================================
   Shortcodes styles
   ========================================================================== */
/* ==========================================================================
   #Popup styles
   ========================================================================== */
.mkdf-menu-popup-holder {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1150;
  visibility: hidden;
  -webkit-transition: opacity 0.2s ease-out 0.1s, visibility 0s 0.4s;
  -moz-transition: opacity 0.2s ease-out 0.1s, visibility 0s 0.4s;
  transition: opacity 0.2s ease-out 0.1s, visibility 0s 0.4s;
}

.mkdf-menu-popup-holder .mkdf-popup-table {
  display: table;
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 9;
}

.mkdf-menu-popup-holder .mkdf-popup-table-cell {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  height: 100%;
  text-align: center;
}

.mkdf-menu-popup-holder .mkdf-popup-inner {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 80%;
  padding: 28px 4% 60px;
  background-color: #fff;
  opacity: 0;
  box-sizing: border-box;
  -webkit-transition: -webkit-transform .2s ease, opacity .3s ease;
  -moz-transition: -moz-transform .2s ease, opacity .3s ease;
  transition: transform .2s ease, opacity .3s ease;
  -webkit-transform: scale(0.8) translateZ(0);
  -moz-transform: scale(0.8) translateZ(0);
  transform: scale(0.8) translateZ(0);
}

.mkdf-menu-popup-holder .mkdf-popup-inner .mkdf-popup-image,
.mkdf-menu-popup-holder .mkdf-popup-inner .mkdf-popup-content {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 50%;
  float: left;
}

.mkdf-menu-popup-holder .mkdf-popup-inner .mkdf-popup-image img {
  display: block;
}

.mkdf-menu-popup-holder .mkdf-popup-inner .mkdf-menu-popup-title {
  text-align: left;
  margin: 0 0 40px;
}

.mkdf-menu-popup-holder .mkdf-popup-inner .mkdf-menu-popup-close {
  position: absolute;
  top: 30px;
  right: 35px;
  font-size: 18px;
}

.mkdf-menu-popup-holder.mkdf-menu-popup-three-columns .mkdf-restaurant-menu-list {
  width: 33.33%;
}

.mkdf-menu-popup-holder.mkdf-menu-popup-two-columns .mkdf-restaurant-menu-list {
  width: 50%;
}

.mkdf-menu-popup-holder.mkdf-menu-popup-one-columns .mkdf-popup-inner {
  width: 34%;
}

.mkdf-menu-popup-holder.mkdf-menu-popup-one-columns .mkdf-restaurant-menu-list {
  width: 100%;
}

.mkdf-menu-popup-opener {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 120px;
  height: 120px;
  background-color: #faf5f0;
  text-align: center;
  border-radius: 50%;
  z-index: 30;
  -webkit-transition: 1.5s 1s cubic-bezier(0.17, 0.57, 0.71, 0.46);
  -moz-transition: 1.5s 1s cubic-bezier(0.17, 0.57, 0.71, 0.46);
  transition: 1.5s 1s cubic-bezier(0.17, 0.57, 0.71, 0.46);
  clip-path: circle(0);
  -webkit-clip-path: circle(0);
}

.mkdf-menu-popup-opener.mkdf-item-appear {
  backface-visibility: hidden;
  clip-path: circle(100%);
  -webkit-clip-path: circle(100%);
}

.mkdf-menu-popup-opener .mkdf-menu-popup-opener-svg .mkdf-svg-smoke-1 {
  -webkit-animation: moving-smoke 2s infinite;
  -moz-animation: moving-smoke 2s infinite;
  animation: moving-smoke 2s infinite;
}

.mkdf-menu-popup-opener .mkdf-menu-popup-opener-svg .mkdf-svg-smoke-2 {
  -webkit-animation: moving-smoke 2s 0.1s infinite;
  -moz-animation: moving-smoke 2s 0.1s infinite;
  animation: moving-smoke 2s 0.1s infinite;
}

@keyframes moving-smoke {
  0%, 100% {
    -webkit-transform: translateY(2px);
    -moz-transform: translateY(2px);
    transform: translateY(2px);
  }
  50% {
    -webkit-transform: translateY(-2px);
    -moz-transform: translateY(-2px);
    transform: translateY(-2px);
  }
}

.mkdf-menu-popup-opener svg {
  display: flex;
  align-items: center;
  height: 80%;
  margin: 0 auto;
}

.mkdf-menu-popup-opener:after {
  content: '';
  position: absolute;
  right: -19px;
  top: 50%;
  width: 35px;
  height: 1px;
  background-color: #a1a1a1;
}

.mkdf-menu-popup-opener.mkdf-menu-popup-light-opener {
  background-color: #fff;
}

.mkdf-menu-popup-opener.mkdf-menu-popup-light-opener:after {
  content: '';
  position: absolute;
  right: 50%;
  top: calc(100% - 16px);
  width: 1px;
  height: 116px;
  background-color: #a1a1a1;
}

/* Popup open/close style */
.mkdf-menu-popup-opened .mkdf-menu-popup-holder {
  opacity: 1;
  visibility: visible;
  -webkit-transition: opacity 0.3s ease;
  -moz-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

.mkdf-menu-popup-opened .mkdf-menu-popup-holder .mkdf-popup-inner {
  opacity: 1;
  -webkit-transform: scale(1) translateZ(0);
  -moz-transform: scale(1) translateZ(0);
  transform: scale(1) translateZ(0);
  -webkit-transition: -webkit-transform 1s cubic-bezier(0.26, 0.63, 0, 0.96);
  -moz-transition: -moz-transform 1s cubic-bezier(0.26, 0.63, 0, 0.96);
  transition: transform 1s cubic-bezier(0.26, 0.63, 0, 0.96);
}

.mkdf-menu-popup-opened .mkdf-restaurant-menu-list {
  float: left;
}

/* ==========================================================================
   #Popup styles - end
   ========================================================================== */
.mkdf-working-hours-holder {
  position: relative;
}

.mkdf-working-hours-holder .mkdf-wh-title {
  text-align: center;
  margin-bottom: 15px;
}

.mkdf-working-hours-holder .mkdf-wh-title .mkdf-wh-title-accent-word {
  color: #a1a1a1;
}

.mkdf-working-hours-holder .mkdf-wh-item {
  position: relative;
  margin-bottom: 12px;
}

.mkdf-working-hours-holder .mkdf-wh-day {
  padding-right: 3px;
  display: table-cell;
  width: 1%;
  white-space: nowrap;
}

.mkdf-working-hours-holder .mkdf-wh-day .mkdf-wh-icon {
  margin-right: 2px;
}

.mkdf-working-hours-holder .mkdf-wh-hours {
  padding-left: 4px;
  display: table-cell;
  width: 1%;
  white-space: nowrap;
}

.mkdf-working-hours-holder .mkdf-wh-dots {
  height: 3px;
  background-image: radial-gradient(circle closest-side, #b3b3b3 99%, rgba(0, 0, 0, 0) 100%);
  background-position: 50% 70%;
  background-size: 6px 2px;
  background-repeat: repeat-x;
  display: table-cell;
  width: 98%;
  z-index: 1;
  position: relative;
}

.mkdf-working-hours-holder.mkdf-wh-with-frame {
  color: #fff;
  padding: 28px;
}

.mkdf-working-hours-holder.mkdf-wh-with-frame.mkdf-wh-with-bg-image {
  background-size: cover;
  background-repeat: no-repeat;
}

.mkdf-working-hours-holder.mkdf-wh-with-frame .mkdf-wh-holder-inner {
  padding: 18px 26px 10px;
  position: relative;
}

.mkdf-working-hours-holder.mkdf-wh-with-frame .mkdf-wh-title {
  color: #fff;
}

.mkdf-working-hours-holder.mkdf-wh-with-frame .mkdf-wh-frame:before, .mkdf-working-hours-holder.mkdf-wh-with-frame .mkdf-wh-frame:after,
.mkdf-working-hours-holder.mkdf-wh-with-frame .mkdf-wh-frame-2:before,
.mkdf-working-hours-holder.mkdf-wh-with-frame .mkdf-wh-frame-2:after {
  position: absolute;
  content: '';
}

.mkdf-working-hours-holder.mkdf-wh-with-frame .mkdf-wh-frame:before {
  top: 0;
  width: 100%;
  height: 3px;
  left: 0;
  border-top: 1px solid #515151;
}

.mkdf-working-hours-holder.mkdf-wh-with-frame .mkdf-wh-frame:after {
  bottom: 0;
  width: 100%;
  height: 3px;
  left: 0;
  border-bottom: 1px solid #515151;
}

.mkdf-working-hours-holder.mkdf-wh-with-frame .mkdf-wh-frame-2:before {
  top: 0;
  left: 0;
  height: 100%;
  width: 3px;
  border-left: 1px solid #515151;
}

.mkdf-working-hours-holder.mkdf-wh-with-frame .mkdf-wh-frame-2:after {
  top: 0;
  right: 0;
  height: 100%;
  width: 3px;
  border-right: 1px solid #515151;
}
